/* albums el double m - homepage */
.album-hidden {
  opacity: 0;
  filter: blur(0.3rem);
  transform: translateX(-100%);
  transition: 0.7s all;
}

.album-show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
  &:hover {
    transition: 0.05s linear;
  }
}

//album others musicians -homepage

.album-hidden-others {
  opacity: 0;
  filter: blur(0.3rem);
  transform: translateX(-100%);
  transition: 0.7s all;
}

.album-show-others {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
  &:hover {
    transition: 0.05s linear;
  }
}

// singles - el double m

.single-hidden {
  opacity: 0;
  filter: blur(0.3rem);
  transform: translateX(-100%);
  transition: 0.7s all;
}

.single-show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
  &:hover {
    transition: 0.05s linear;
  }
}
// singles - other musicians

.single-hidden-others {
  opacity: 0;
  filter: blur(0.3rem);
  transform: translateX(-100%);
  transition: 0.7s all;
}

.single-show-others {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
  &:hover {
    transition: 0.05s linear;
  }
}

//for users who have set reduced motion in browser
@media (prefers-reduced-motion) {
  .album-hidden,
  .album-hidden-others,
  .album-show .album-show-others,
  .single-hidden,
  .single-show,
  .single-hidden-others,
  .single-show-others {
    transition: none;
  }
}
